            // 将服务数据加在listData里面 // 数据深拷贝
            this.listData.serverTransportList = JSON.parse(JSON.stringify(this.listData.routeLists))
            this.listData.serverStationList = JSON.parse(JSON.stringify(this.listData.routeLists))
            this.listData.serverCustomsList = JSON.parse(JSON.stringify(this.listData.routeLists))
            this.listData.serverBondList = JSON.parse(JSON.stringify(this.listData.routeLists))
